@import url(https://fonts.googleapis.com/css?family=Montserrat+Alternates);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  width: 100%;
  margin: 0 auto;
  background: #272727;
  font-family: "Montserrat Alternates", sans-serif;
}
.Wrapper-Menu {
  display: flex;
  width: 100%;
  height: 70px;
  justify-content: flex-end;
  align-content: center;
  border-bottom: 1px dashed #ffffff;
  background: rgba(255, 255, 255, 0.075);
  margin-bottom: 60px;
}
.Wrapper-Menu .Menu-Categories {
  display: flex;
  color: white;
  list-style-type: none;
  align-content: center;
  padding-right: 115px;
  margin-top: 20px;
}
.Wrapper-Menu .Menu-Categories .Category {
  list-style-type: none;
  padding: 0 5px;
}
.Wrapper-Menu .Menu-Categories .Category a {
  text-decoration: none;
  font-weight: bold;
  color: white;
  font-size: 20px;
}
.Wrapper-Menu .Menu-Categories .Category a:hover {
  color: #999595;
}
.Wrapper-Menu .Menu-Categories span {
  font-size: 20px;
}
.About-Me-Page {
  display: flex;
  width: 100%;
  justify-content: center;
}
.About-Me-Page .Contact-Information {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
  background: rgba(0, 0, 0, 0.185);
  border-right: 1px solid lightgray;
  margin: 50px 0;
  padding: 50px 0 50px 40px;
}
.About-Me-Page .Contact-Information .My-Photo {
  background: url(/my-resume/static/media/photo.74be3c0c.jpg) no-repeat;
  background-size: contain;
  height: 215px;
  padding-top: 10px;
}
.About-Me-Page .Contact-Information .My-Name {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 45px;
}
.About-Me-Page .Contact-Information .My-Name .First-Name {
  font-weight: bold;
  font-size: 44px;
  padding: 0;
  margin: 0;
  color: white;
}
.About-Me-Page .Contact-Information .My-Name .Surname {
  font-size: 24px;
  padding: 0;
  margin: 0;
  color: white;
}
.About-Me-Page .Contact-Information .My {
  padding-top: 20px;
  margin-right: 30px;
}
.About-Me-Page .Contact-Information .My .Contact-Information-Header {
  margin-bottom: 0;
  font-weight: bold;
  font-size: 20px;
  color: white;
}
.About-Me-Page .Contact-Information .My .Specific-Information {
  margin-top: 0;
  font-size: 16px;
  color: white;
}
.About-Me-Page .About-Me-Information {
  display: flex;
  flex-direction: column;
  margin: 50px 0;
  padding: 0 5px 0 0;
}
.About-Me-Page .About-Me-Information .Education-Experience-And-Skills {
  align-content: center;
}
.About-Me-Page
  .About-Me-Information
  .Education-Experience-And-Skills
  .Information-Section-Name {
  margin: 20px 0 0 0;
  padding-left: 20px;
  background: #3f3f3f;
  font-weight: bold;
  font-size: 30px;
  color: white;
}
.About-Me-Page
  .About-Me-Information
  .Education-Experience-And-Skills
  .Full-Description {
  margin: 0;
  padding: 0 0 10px 20px;
  background: #3f3f3f;
  font-size: 20px;
  color: white;
}
.About-Me-Page
  .About-Me-Information
  .Education-Experience-And-Skills:last-child {
  margin-bottom: 20px;
}
.Projects-Page {
  display: flex;
  justify-content: center;
  padding-top: 100px;
}
.Projects-Page .Tree {
  background: url(/my-resume/static/media/tree.84373aac.png) no-repeat;
  background-size: contain;
  height: 300px;
  width: 200px;
}
@media screen and (max-width: 550px) {
  .Wrapper-Menu {
    padding: 0;
    margin-bottom: 20px;
  }
  .Wrapper-Menu .Menu-Categories {
    align-self: center;
    padding-right: 10px;
    margin-right: 10px;
  }
  .About-Me-Page {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .About-Me-Page .Contact-Information {
    padding: 20px 0 20px 45px;
    border-right: none;
    margin-bottom: 0;
  }
}

